






































import Vue from "vue";
import Component from "vue-class-component";
import { AppService } from "../service/app.service";
import CafeCreateBanner from "./cafe/CafeCreateBanner.vue";

@Component({
  components: { CafeCreateBanner },
})
export default class App extends Vue {
  app = AppService.instance;

  logout(): void {
    console.log("App::logout()");
    this.app.api.logout();
    this.$store.commit("logout");
  }

  openCafe(id: string): void {
    location.href = "https://" + id + ":" + location.port + "/cafe/admin/edit";

    // <router-link :to="'/cafe/admin/edit/' + cafe.id">{{
    //   cafe.id
    // }}</router-link>
  }
}
