

















import Vue from "vue";
import Component from "vue-class-component";
import router from "@/router";
import TopMenuUser from "./components/TopMenuUser.vue";
import MobileHeader from "./components/MobileHeader.vue";
import DesktopRightSidebar from "./components/DesktopRightSidebar.vue";
import DesktopMainMenuLeft from "./components/desktop/DesktopMainMenuLeft.vue";
import DesktopMainMenuRight from "./components/desktop/DesktopMainMenuRight.vue";
import { AppService } from "./service/app.service";
import { ApiService } from "./x-vue/services/api.service";
import { FirebaseService } from "./x-vue/services/firebase.service";
import DesktopHeader from "./components/desktop/DesktopHeader.vue";

@Component({
  components: {
    TopMenuUser,
    MobileHeader,
    DesktopRightSidebar,
    DesktopMainMenuLeft,
    DesktopMainMenuRight,
    DesktopHeader,
  },
})
export default class App extends Vue {
  app = AppService.instance;
  api = ApiService.instance;

  fb = FirebaseService.instance;

  async mounted(): Promise<void> {
    // console.log("App::mounted. running mode: ", process.env.NODE_ENV);
    this.app.portal();
    this.fb.init({
      apiKey: "AIzaSyDWiVaWIIrAsEP-eHq6bFBY09HLyHHQW2U",
      authDomain: "sonub-version-2020.firebaseapp.com",
      databaseURL: "https://sonub-version-2020.firebaseio.com",
      projectId: "sonub-version-2020",
      storageBucket: "sonub-version-2020.appspot.com",
      messagingSenderId: "446424199137",
      appId: "1:446424199137:web:f421c562ba0a35ac89aca0",
      measurementId: "G-F86L9641ZQ",
    });
    router.afterEach((to) => {
      if (
        to.name == "PostList" ||
        to.name == "PostView" ||
        to.name == "PostEdit" ||
        to.path.indexOf("advertisement") > -1 ||
        to.path.indexOf("admin") > -1 ||
        to.path.indexOf("cafe") > -1
      ) {
        this.app.hideLeftSidebar();
      } else {
        this.app.showLeftSidebar();
      }
    });
  }
}
